<template>
  <div>
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button
        icon="el-icon-plus"
        size="small"
        @click="handleAdd('add')"
        type="warning"
      >{{$t('common.add')}}</el-button>
    </div>
    <el-form :model="searchForm" ref="searchForm" label-width="82px" size="small">
      <el-row class="label-first" :gutter="20">
        <el-col :xl="4" :lg="6">
          <el-form-item :label="$t('coreApp.tableVersion.versionNum')+'：'">
            <el-input type="text" v-model.trim="searchForm.versionNo" maxlength="20" clearable></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="4" :lg="6">
          <el-form-item :label="'应用类型：'">
            <el-select v-model="searchForm.moType" clearable style="width:100%">
              <el-option
                v-for="item in moTypeListOptions"
                :key="item.value"
                :label="item.name"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xl="4" :lg="6">
          <el-button
            type="primary"
            size="small"
            @click="search('search')"
            icon="el-icon-search"
          >{{$t('common.search')}}</el-button>
        </el-col>
      </el-row>
    </el-form>
    <el-table stripe v-loading="loading" :data="tableForm" style="width: 100%">
      <el-table-column
        align="center"
        prop="name"
        :label="$t('coreApp.tableVersion.versionName')"
        width="120"
      ></el-table-column>
      <el-table-column
        align="center"
        prop="moType"
        :label="'应用类型'"
        width="150">
        <template slot-scope="props">{{props.row.moType | filterCommonMoType(moTypeListOptions,'value')}}</template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="versionNo"
        :label="$t('coreApp.tableVersion.versionNum')"
        width="120"
      ></el-table-column>
      <el-table-column
        align="center"
        prop="fileType"
        :label="$t('coreApp.tableVersion.type')"
        width="150"
      >
        <template slot-scope="props">{{props.row.fileType === 'FULL' ? '全量' : '增量'}}</template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="createTime"
        :label="$t('coreApp.tableVersion.createTime')"
        width="160"
      ></el-table-column>
      <el-table-column
        align="center"
        prop="creator"
        :label="$t('coreApp.tableVersion.createPerson')"
        width="120"
      ></el-table-column>
      <el-table-column
        align="center"
        prop="path"
        :label="$t('coreApp.tableVersion.path')"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        align="center"
        prop="remark"
        :label="$t('coreApp.tableVersion.desc')"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column :label="$t('common.operation')" width="90" align="center">
        <template slot-scope="scope">
          <el-dropdown trigger="click">
            <el-button type="text" size="mini">操作
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>
                <el-button
                  type="text"
                  size="mini"
                  icon="el-icon-edit"
                  @click="handleEdit(scope.row)"
                >{{$t('common.edit')}}</el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button
                  type="text"
                  size="mini"
                  icon="el-icon-download"
                  @click="download(scope.row)"
                >{{$t('common.download')}}</el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button
                  type="text"
                  class="btn-text-red"
                  size="mini"
                  icon="el-icon-delete"
                  @click="deleteRow(scope.row)"
                >{{$t('common.delete')}}</el-button>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>
    <!-- 翻页部分 -->
    <div class="block">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page="pageNo"
        :page-size="10"
        layout="total, prev, pager, next, jumper"
        background
        :total="total"
      ></el-pagination>
    </div>
    <!-- add弹出框 -->
    <el-dialog
      title="添加应用版本"
      @close="cancel('versionForm')"
      :visible.sync="addVisible"
      width="40%"
      center>
      <el-form
        ref="versionForm"
        :model="versionForm"
        :rules="inputRules"
        status-icon
        label-width="100px"
        size="small">
        <el-row :gutter="10">
          <el-col :span="24">
            <el-form-item label="软件包来源" prop="method">
              <el-radio-group v-model="versionForm.method" @change="radioChange">
                <el-radio label="Local">{{$t('common.local')}}</el-radio>
                <el-radio label="Remote">{{$t('common.server')}}</el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10" v-show="versionForm.method === 'Local'">
          <el-col :span="19">
            <el-form-item
              :label="$t('coreApp.tableVersion.path')"
              prop="radio"
              ref="inputFileValue"
            >
              <el-input v-model="radio" placeholder="请输入文件"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="4">
            <input
              :value="inputFile"
              type="file"
              class="input-file"
              @change="handleChange($event)"
              accept=".zip, .tar, .gz"
            >
            <el-button type="primary" size="mini">{{$t('common.viewFile')}}</el-button>
          </el-col>
        </el-row>
        <el-row :gutter="10" v-show="versionForm.method === 'Remote'">
          <el-col :span="23">
            <el-form-item :label="$t('coreApp.tableVersion.path')" prop="path" ref="inputFileDom">
              <el-input
                v-model.trim="versionForm.path"
                clearable
                placeholder="请输入路径(例 D:\d_pan\nginx-1.15.3)"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :span="24">
            <el-form-item :label="$t('coreApp.tableVersion.type')" prop="radio">
              <el-radio-group v-model="versionForm.fileType">
                <el-radio label="FULL">全量</el-radio>
                <el-radio label="INCR">增量</el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :span="24">
            <el-form-item :label="'应用类型'" prop="moType">
              <el-select v-model="versionForm.moType" clearable style="width:100%">
                <el-option
                  v-for="item in moTypeListOptions"
                  :key="item.value"
                  :label="item.name"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :span="24">
            <el-form-item :label="$t('coreApp.tableVersion.versionName')" prop="name">
              <el-input v-model.trim="versionForm.name" type="text" placeholder="请输入1-50位字符" maxlength="50" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item :label="$t('coreApp.tableVersion.versionNum')" prop="versionNo">
              <el-input v-model.trim="versionForm.versionNo" type="text" placeholder="请输入1-15位版本号" maxlength="15" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :span="24">
            <el-form-item :label="$t('coreApp.tableVersion.desc')">
              <el-input v-model.trim="versionForm.remark" type="textarea" maxlength="100" placeholder="请输入1-100位字符"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10" style="padding-top:9px">
          <el-col :span="23">
            <el-form-item :label="$t('coreApp.tableVersion.readFileProgress')">
              <el-progress
                :text-inside="true"
                :stroke-width="18"
                :percentage="percentageNum"
                status="success"
                style="padding-top:9px"
              ></el-progress>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="saveAdd" :loading="loading" type="primary" size="small">{{$t('common.ok')}}</el-button>
        <el-button @click="cancel('versionForm')" size="small">{{$t('common.cancel')}}</el-button>
      </span>
    </el-dialog>
    <!-- edit弹出框 -->
    <el-dialog
      :title="'修改应用版本'"
      @close="cancel('editForm')"
      :visible.sync="editVisible"
      width="40%"
      center>
      <el-form
        ref="editForm"
        :model="editForm"
        :rules="inputRules"
        status-icon
        label-width="100px"
        size="small">
        <el-row :gutter="10">
          <el-col :span="23">
            <el-form-item :label="$t('coreApp.tableVersion.versionName')" prop="name">
              <el-input v-model.trim="editForm.name" placeholder="请输入1-50位字符" maxlength="50" clearable>></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item :label="'应用类型'" prop="moType">
              <el-select v-model="editForm.moType" disabled style="width:100%">
                <el-option
                  v-for="item in moTypeListOptions"
                  :key="item.value"
                  :label="item.name"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :span="23">
            <el-form-item :label="$t('coreApp.tableVersion.versionNum')" prop="versionNo">
              <el-input v-model.trim="editForm.versionNo" disabled></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :span="23">
            <el-form-item :label="$t('coreApp.tableVersion.type')" prop="fileType">
              <el-input :value="editForm.fileType === 'FULL' ? '全量' : '增量'" disabled></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :span="23">
            <el-form-item :label="$t('coreApp.tableVersion.createTime')" prop="createTime">
              <el-input v-model.trim="editForm.createTime" disabled></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :span="23">
            <el-form-item :label="$t('coreApp.tableVersion.createPerson')" prop="creator">
              <el-input v-model.trim="editForm.creator" disabled></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :span="23">
            <el-form-item :label="$t('coreApp.tableVersion.path')" prop="path">
              <el-input v-model.trim="editForm.path" disabled></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :span="23">
            <el-form-item :label="$t('coreApp.tableVersion.desc')">
              <el-input v-model.trim="editForm.remark" type="textarea" placeholder="请输入1-100位字符" maxlength="100"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="saveEdit()" :loading="loading" type="primary" size="small">{{$t('common.ok')}}</el-button>
        <el-button @click="cancel('editForm')" size="small">{{$t('common.cancel')}}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Api from '@/constants/Api.js'
import { mapState, mapActions } from 'vuex'
export default {
  data() {
    return {
      pageNo: 1, // 当前页数
      limit: 5, // 每页条数
      total: 0, // 总页数
      // 查询规则
      inputRules: {
        path: [{ required: true, message: '请输入路径', trigger: 'blur' }],
        method: [{ required: true, message: '请选择', trigger: 'blur' }],
        radio: [{ required: true, message: '请选择文件', trigger: 'blur' }],
        name: [{ required: true, message: '请输入版本名称', trigger: 'blur' }],
        moType: [
          { required: true, message: '请输入应用类型', trigger: 'blur' }
        ],
        versionNo: [
          { required: true, message: '请输入版本号', trigger: 'blur' }
        ],
        fileType: [{ required: true, message: '请输入版本名称', trigger: 'blur' }],
        createTime: [{ required: true, message: '请输入版本名称', trigger: 'blur' }],
        creator: [{ required: true, message: '请输入版本号', trigger: 'blur' }]
      },
      loading: false,
      searchForm: {},
      tableForm: [],
      // 添加模板
      versionForm: {
        method: 'Local'
      },
      editForm: {},
      addVisible: false,
      percentageNum: 0,
      editVisible: false,
      radio: '',
      inputFile: '',
      idx: -1
    }
  },
  computed: {
    ...mapState({
      moTypeListOptions: state => state.core.moTypeListOptions
    }),
    query() {
      let { moType, versionNo } = this.searchForm
      let params = Object.assign(
        {},
        {
          pageNo: this.pageNo,
          limit: this.limit,
          baseParam: { moType, versionNo }
        }
      )
      return params
    }
  },
  watch: {
  },
  async activated() {
    const vm = this
    if (this.moTypeListOptions.length <= 0) {
      await vm.coreGetData({vm})
      vm.search()
    } else {
      await this.search()
    }
  },
  methods: {
    ...mapActions('core', ['coreGetData']),
    // 翻页事件
    handleCurrentChange(val) {
      this.currentPage = val
      this.routintList = this.dataBox[val - 1]
    },
    search(title) {
      this.loading = true
      this.$axios
        .post(Api.coreVersionFind, this.query)
        .then(res => {
          if (title === 'search') {
            this.$message.success(`查询成功`)
          }
          this.loading = false
          let data = JSON.parse(JSON.stringify(res.data))
          this.tableForm = data.data
          this.total = data.recordsTotal
        })
        .catch(error => {
          this.loading = false
          this.$message.error(error.data.message || error.message || error)
        })
    },
    // 添加
    handleAdd() {
      this.versionForm = {
        method: 'Local',
        fileType: 'FULL',
        creator: window.localStorage.getItem('userName')
      }
      this.inputFile = ''
      this.radio = ''
      this.percentageNum = 0
      this.addVisible = true
    },
    // 添加确定
    saveAdd() {
      let {
        method,
        sourceFile,
        moType,
        name,
        fileType,
        versionNo,
        path,
        creator,
        remark
      } = this.versionForm
      if (method === 'Local') {
        if (!moType || !name || !versionNo || !sourceFile) {
          this.$message({
            type: 'warning',
            message: '带*不能为空'
          })
          return
        }
      } else {
        if (!moType || !name || !versionNo || !path) {
          this.$message({
            type: 'warning',
            message: '带*不能为空'
          })
          return
        }
      }
      let query = Object.assign(
        {},
        { sourceFile, moType, name, fileType, versionNo, path, creator, remark }
      )
      let formData = new FormData()
      Object.keys(query).forEach(value => {
        if (query[value]) {
          formData.append(value, query[value])
        }
      })
      this.loading = true
      this.$axios({
        method: 'post',
        url: Api.coreVersionAdd,
        params: {
          method
        },
        data: formData,
        onUploadProgress: progressEvent => {
          if (progressEvent.lengthComputable) {
            this.percentageNum = Math.round(
              (progressEvent.loaded / progressEvent.total) * 100
            )
          }
        }
      })
        .then(res => {
          if (res.code === '0000') {
            this.search()
            this.$message.success(`添加成功`)
            this.addVisible = false
          }
        })
        .catch(error => {
          this.loading = false
          this.$message.error(error.data.message || error.message || error)
        })
    },
    handleEdit(value) {
      this.editForm = JSON.parse(JSON.stringify(value))
      this.editVisible = true
    },
    saveEdit() {
      let { id, name, remark } = this.editForm
      if (!name) {
        this.$message({
          type: 'warning',
          message: '版本名称不能为空'
        })
        return
      }
      let query = Object.assign({}, { id, name, remark })
      this.loading = true
      this.$axios({
        method: 'post',
        url: Api.coreVersionUpdate,
        data: query
      })
        .then(res => {
          if (res.code === '0000') {
            this.search()
            this.$message.success(`编辑成功`)
            this.editVisible = false
          }
        })
        .catch(error => {
          this.loading = false
          this.$message.error(error.data.message || error.message || error)
      })
    },
    cancel(formName) {
      const vm = this
      if (formName === 'versionForm') {
        vm.inputFile = ''
        vm.$refs.versionForm.resetFields()
        vm.addVisible = false
      } else {
        vm.$refs.editForm.resetFields()
        vm.editVisible = false
      }
    },
    deleteRow(value) {
      this.conmmonMethods.makePopBox(this, '删除').then(() => {
        this.loading = true
        let { id } = value
        this.$axios({
          method: 'post',
          url: Api.coreVersionDelete,
          data: { id }
        })
          .then(res => {
            if (res.code === '0000') {
              this.search()
              this.$message.success(`删除成功`)
              this.addVisible = false
            }
          })
          .catch(error => {
            this.loading = false
            this.$message.error(error.data.message || error.message || error)
          })
      })
    },
    download(value) {
      this.conmmonMethods.makePopBox(this, '下载').then(() => {
        if (value) {
          let box = value.path.split('/')
          let name = box[box.length - 1]
          this.$axios({
            method: 'get',
            url: Api.coreVersionDownload + '?path=' + value.path,
            responseType: 'blob'
          }).then(res => {
            this.commonFunc.funDownload(res, name)
          })
        }
      })
    },
    handleChange(e) {
      // let box = ['application/zip', 'application/x-gzip', 'application/x-tar']
      let filtName = e.target.files[0].name
      if (
        filtName.endsWith('.zip') ||
        filtName.endsWith('.tar') ||
        filtName.endsWith('.tar.gz')
      ) {
        this.versionForm.sourceFile = e.target.files[0]
        this.radio = e.target.value
      } else {
        this.$message({
          type: 'warning',
          message: '文件类型不正确,请重新选择文件类型(.tar / .tar.gz / .zip)'
        })
      }
    },
    radioChange(val) {
      this.versionForm.method = val
      this.$nextTick(() => {
        if (this.$refs.inputFileValue) {
          this.$refs.inputFileValue.resetField()
        }
        if (this.$refs.inputFileDom) {
          this.$refs.inputFileDom.resetField()
        }
        this.inputFile = ''
        this.radio = ''
        this.versionForm.sourceFile = ''
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.input-file {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
</style>



